import EventManager from "../../../eventManager/EventManager";
import { GameEvents } from "../../../gameEvents/GameEvents";
import { BaseState } from "../../../state-machine/BaseState";
import { GameStates } from "../../gameStateMachine/StatesEnum";

export class BattleState extends BaseState {

  begin(data?: any) {
    EventManager.dispatch(GameEvents.BATTLE_START);
    EventManager.add(GameEvents.BATTLE_END, this.end);
  }

  end = (): void => {
    this.stateMachine.setState(GameStates.FINISH);
  }

  cleanUp(): void {

  }
}